  template: `<td-base-chart [style.height.%]="100"
                [data]="data"
                [yAxisType]="'value'"
                [xAxisType]="'category'"
                [dataZoom]="false"
                [dataZoom]="false">
  <ng-content></ng-content>
</td-base-chart>`,
